import { Component, Input, OnChanges, SimpleChanges } from "@angular/core";
import { latLng, Layer } from "leaflet";
import * as L from "leaflet";

@Component({
  selector: "app-map",
  templateUrl: "./map.component.html",
  styleUrls: ["./map.component.css"],
})
export class MapComponent implements OnChanges {
  @Input() markers: any; // Adjust the type according to your data structure

  options = {
    layers: [
      L.tileLayer("https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png", {
        maxZoom: 18,
        minZoom: 1,
        attribution:
          '&copy; <a href="http://www.openstreetmap.org/copyright">OpenStreetMap</a>',
      }),
    ],
    zoom: 3,
    center: latLng(48.59378, 9.35982),
  };

  layers: Layer[] = [];

  constructor() {}

  ngOnChanges(changes: SimpleChanges): void {
    if (changes["markers"]) {
      this.updateMarkers();
    }
  }

  private updateMarkers(): void {
    this.layers = []; // Reset layers
    if (this.markers && this.markers.items) {
      this.markers.items.forEach((item) => {
        if (
          item.getLatitude() !== undefined &&
          item.getLongitude() !== undefined
        ) {
          const newCircle = L.circle(
            [item.getLatitude(), item.getLongitude()],
            {
              color: item.getColor(),
              fillColor: item.getColor(),
              fillOpacity: 0.5,
              radius: 500,
            }
          );
          this.layers.push(newCircle);
        }
      });
    }
  }
}
